/*
Extra Styles

PLEASE NOTE:
This is where all the magic happens.

These following lines are the compiled result of changes made in the "/resources/scss/_extra_styles.scss" file in the Bootstrap 4 project folder. This .SCSS file is where all the special styling, mixins, default NCM attributes and all bespoke variables are created.

Making changes in the .SCSS file instead of the compiled .CSS file helps keep the code as clean as possible. Also, it's pretty much standard, accepted best practice!

PLEASE PLEASE PRETTY PLEASE DO NOT MAKE ANY CHANGES TO ANY CSS OR SCSS FILES ANYWHERE ELSE!!! It's almost impossible to reverse engineer .CSS code back into .SCSS format!!!
- Love, Afotey Annum
*/

//NCM-specific fonts
$font-family-sans-serif:      Arial, sans-serif !default;

// button colors (NCM specific)
$red:       #d52b1e !default;
$gray:      #333333 !default;
$btn-gray:  #d8dada !default;
$pill-blue: #0088ce !default;
$orange:    #ed7000 !default;
$yellow:    #ffbc3d !default;
$pink2:     #ffc0cb !default;

// border radius reset
$border-radius: 0 !default;

// background color for Switch Tech Dev List
$lighter_gray1: #f6f6f6;

// help page image box shadow variables
$help-box-shadow: 0.5125rem 0.5125rem 0.5125rem 0 rgba(0,0,0,0.75);

// general button box shadow variables
$button-box-shadow: 0.25rem 0.25rem 0.25rem 0 rgba(0,0,0,0.30);

// NCM-specific box border styling
$NCM-box-bordering: 0.09rem solid $btn-gray;

// main nav bordering
$main-nav-border: 0.015rem solid $btn-gray;

//mixins
@mixin table-backup {
  font-size: 0.70rem;
}
@mixin default-box-shadow {
  -webkit-box-shadow: $button-box-shadow;
  -moz-box-shadow: $button-box-shadow;
  box-shadow: $button-box-shadow;
}
@mixin nav-pill-conflict {
  background-color: transparent;
  border: 0.1875rem solid $red;
  color: $black !important;
  padding: 0.5rem 0.109rem;
}
@mixin nav-link-border-radius {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}
@mixin top-nav-border {
  border-left: $main-nav-border;
  // border-top: ($main-nav-border) + 2rem;
  border-top: $main-nav-border;
  border-right: $main-nav-border;
  border-bottom: none;
}
@mixin clear-border-radius {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
@mixin no-box-shadow {
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
}
// mixins

html {
// html fix for sticky footer
  position: relative;
  min-height: 100%;
}

// body height
body {
  font-size: 0.8rem;
// Margin bottom by footer height
  margin-bottom: 3.750rem;
}

.login-page {
  margin: auto;
  max-width: 22.5rem;
  padding: 8% 0 0;
    h4 {
      text-align: center;
    }
    .warning {
          font-weight: 600;
        }
    button {
      background-color: $red;
      color: $white;
      font-weight: 600;;
    }
    .form {
      margin: 0 auto 6.25rem;
      padding: 2.813rem;
      border: 0.0625rem solid $btn-gray;
      p {
        font-weight: 600;
        margin-bottom: 0.5rem;

      }
      button {
        margin-top: 1rem;
      }
      .input {
        border-radius: 0.3125rem;
        margin: 0 0 1.25rem;
        width: 100%;
      }
      .message {
        padding-top: 1.5rem;
        text-align: center;
      }
    }
}

.main-header {
  .navbar {
    border-bottom: 0.02rem solid $btn-gray;
    margin-bottom: 1rem;
    padding: 0;
    .navbar-toggler-icon {
      width: 1em;
    }
  }
  .top-menu {
    .navbar-brand {
      display: inline-block;
      padding: 1.25rem 0 0.3125rem 0.9rem;
      margin-right: 1rem;
      font-size: 1.25rem;
      line-height: inherit;
      white-space: nowrap;
      &:focus, &:hover {
        tex-decoration: none;
      }
    }

    .nav > li {
      display: block;
      position: relative;
      padding: 1.25rem;
      @include border-radius;
      &:hover {
        background-color: $btn-gray;
        text-decoration: none;
      }
    }
    .navbar-nav {
      display: inline-flex;
      flex-direction: unset;
    }
  }
  .box {
    width: 50%;
    h4 {
      border-left: 0.1rem solid $btn-gray;
      display: inline-block;
      font-size: 1.35rem;
      margin: 1.65rem 0 0 0.5rem;
      padding-left: 0.3rem;
      position: absolute;
      @include media-breakpoint-only(xs) {
        border: none;
        display: block;
        margin: 0;
        position: relative;
      }
    }
  }
  .logo-box {
    img {
      margin-top: 1.5rem;
      @include media-breakpoint-between(xs, md) {
        margin-top: 1rem;
        }
      }
  }
  .profile-box {
    text-align: right;
    padding: 0.625rem 0.625rem 0 0;
    @include media-breakpoint-between(xs, md) {
      padding: 0;
    }
  }
}

// navigation styles
  .nav-link {
    color: $pill-blue !important;
  }
  .navbar-collapse {
    &.show {
      ul {
        width: 90vw;
        background-color: transparent;
      }
      .navbar-nav {
        .nav-link {
          border-left: none;
          border-right: none;
        }
      }
    }
  }
.nav-tabs {
// main nav styling
  background-color: $lighter_gray1;
  border-bottom: none;
  font-size: 0.9375rem;
  padding: 0;
  .nav-item {
    margin-bottom: 0 !important;
  }
  .nav-link {
// main nav link styling
    @include top-nav-border;
    color: $pill-blue;
    @include clear-border-radius;
    &:hover {
      background-color: #fff !important;
      border-top: 0.125rem solid $red;
    }
  }
  .nav-link.active, .nav-item.show .nav-link {
    color: $red !important;
    border-radius: 0 !important;
    border-bottom: none;
    border-top: 0.15rem solid $red;
    font-weight: 800;
  }
}

// ACTUAL breadcrumbs styles
.real-crumb {
  background-color: transparent !important;
  margin-left: 1rem;
  span {
    color: $black;
  }
}
.breadcrumb-item + .breadcrumb-item::before {
  content: ">";
}

// NAVIGATION breadcrumb styles
.breadcrumb {
  background-color: #f6f6f6;
  color: $pill-blue;
  font-size: small;
  padding: 0;
  .nav-item {
    border-left: 0.0125rem solid #dddfe2;
    border-right: 0.0125rem solid #dddfe2;
    // border-left: none;
    &.show {
      .nav-link {
        width: 100%;
      }
    }
    .dropdown {
      background-color: none;
    }
      .dropdown-toggle {
        color: $pill-blue;
        margin-right: 0;
        // @media screen and (-webkit-min-device-pixel-ratio:0){
          // &:first-child {
          //   margin-right: 0.1rem !important;
          // }
        // }
        &::hover {
          border: none !important;
          margin-right: 0 !important;
          width: 100%;
        }
        &.active {
          background-color: $white;
          color: $red;
          @include border-radius;
        }
      }
  }
}

// nav pill styles
.nav-pills {
  .help {
    &.active {
      background-color: $red;
    }
  }
  .nav-link.active {
    box-shadow: none;
    color: $white !important;
  }
  .btn-conflicts {
    padding: 0.5rem;
    &.active {
      @include nav-pill-conflict;
    }
  }
  @include media-breakpoint-between(xs, md) {
    text-align: center !important;
  }
}

