// custom NCM form styles for input fields

// special NCM-specfic borders for all form elements like text areas, inputs and dropdowns
.border-boxing {
  border-top: $NCM-box-bordering;
  border-right: $NCM-box-bordering;
  border-left: $NCM-box-bordering;
}
.form-control {
  @extend .border-boxing;
  border-bottom: 0.09rem solid $black;
  border-radius: 0;
}
// special NCM-specfic borders for all form elements like text areas, inputs and dropdowns

.form-control-plaintext {
  padding: 0;
}
@include media-breakpoint-only(xs) {
  .form-control::placeholder {
    font-size: xx-small;
  }
}
.form-editable-fields, .form-non-editable-fields {
  .form-control {
    background-color: $pink2;
    display: inline;
    margin: 0.25rem 0.25rem;
