  }
  label {
    font-weight: 600;
    margin-bottom: 0.1875rem;
// fix for Google Chrome wrapping input field labels
    white-space: nowrap;
  }
  .form-control-plaintext {
    color: $black;
    line-height: 1;
    width:100%;
      &:focus {
        border: none !important;
      }
  }
}
.big-modal {
