// navigation dropdown styling
.dropdown-nav-menu {
  li.dropdown {
    position: static;
    float: right;
    &:hover {
      background: $white;
      // @include nav-link-border-radius;
    }
    &.show {
      border-top: 0.125rem solid $red;
      // @include nav-link-border-radius;
        a:focus {
          outline: none;
        }
    }
    .dropdown-item {
      border-bottom: 0.125rem solid $red !important;
    }
    .dropdown-toggle {
      background: none;
      color: $blue;
    }
  }
  .dropdown-menu {
    width: auto;
    a {
      background: $white;
      display: inline !important;
      margin: 0 0.25rem;
      &:hover {
        border: none;
        background-color: $btn-gray;
      }
    }
    > li {
      display: inline-block;
      &:hover {
        border: none;
      }
    }
  }
}

